<template>
  <div class="personal-view-layout">
    <header class="login-header-bar clearfix">
      <a onclick="window.history.go(-1);" class="login-back-btn"></a>
      <h2 class="login-title">最近浏览</h2>
    </header>
    <main class="personal-view-main clearfix" v-show="!showNoData">
        <ul>
          <li class="tiezi-item" v-for="item in tieZiData">
            <div class="line">
            </div>
            <div class="who-tiezi-item1 clearfix">
              <img v-lazy="item.avatar" alt="" class="personal-review-pic">
              <div class="who-publish-box">
                <p class="who-publish-name">
                  {{item.author}}
                </p>
                <div class="who-publish-times">
                                <span class="who-publish-time">
                                   {{item.created_at | filterTime}}
                                </span>
                  <span class="who-publish-tag" v-show="tags.length>0">
                                   来自标签：
                                    <span v-for="(tag,index) in item.tags">{{tag.name}}<span v-show="(index+1)!==item.tags.length">、</span></span>
                                </span>

                </div>
              </div>
            </div>
            <div class="who-tiezi-item2">
              <router-link :to="'/tiezidetail/'+item.id" v-if="item.article_type===1 || item.article_type==='1'" class="who-tiezi-info">
                <p class="index-tiezi-title">
                  {{item.title}}
                </p>
                <p class="index-tiezi-content" v-html="item.body">
                  {{item.body}}
                </p>

              </router-link>
            </div>
            <div class="who-tiezi-item3 clearfix">
              <div class="tiezi-read-box">
                <i class="read-icon"></i>
                <span class="tiezi-read-num">
                                {{item.article_views_count}}
                            </span>
              </div>
              <div class="tiezi-zanfh-box clearfix">
                <div class="tiezi-hui-box">
                                <span class="tiezi-hui-icon">
                                </span>
                  <span class="tiezi-hui-text">
                                    {{item.article_comments_count}}
                                </span>
                </div>
                <div class="tiezi-zan-box">
                                <span class="tiezi-zan-icon">
                                </span>
                  <span class="tiezi-zan-text">
                                   {{item.article_likes_count}}
                                </span>
                </div>
              </div>
            </div>
          </li>

        </ul>
      <ul class="news-item-box shouc-news-item">
        <li  track-by="$index" v-for="(item,index) in newsData" class="news-item" v-show="item.status===1 || item.status==='1'">
          <router-link  class="news-item-link" :to="'/newsdetail/'+item.id">
            <img v-lazy="item.thu_image" alt="" class="news-icon">
            <div class="news-info-container">
              <p class="news-title">
                {{item.title}}
              </p>
              <div class="news_time_tag">
                <span class="news_time">{{item.created_at}}</span>
                <span class="news_tag">来自标签：<span v-for="(tag,index) in item.tags">{{tag.name}}<span v-show="(index+1)!==item.tags.length">、</span></span></span>

              </div>
              <div class="news_comment_read">
                <span class="news_comment">评论{{item.article_comments_count}}</span>
                <span class="news_read">阅读量{{item.article_views_count}}</span>
              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </main>
    <NoData v-show="showNoData"></NoData>
  </div>
</template>
<script>
  import Api from '../../../api/api';
  import {getRecentView} from '../../../api/pcreviews';
  import NoData from '../../nodata/NoData.vue';
  export default{
      components:{
        NoData
      },
      data(){
        return{
          articles:[],
          newsData:[],
          tieZiData:[],
          showNoData:false,
          tagName:"",
          tags:[],
        }
      },
    filters:{
      filterTime(value){
        return value.split("T")[0];
      }
    },
      mounted(){
          this.getViewDatas();
      },
      methods:{
        getViewDatas(){
            const _this = this;
            getRecentView(`${Api.MYTIEZI}${localStorage.getItem('userId')}/views/`).then((res)=>{
                _this.initDatas(res);
              },(err)=>{
                _this.delErr();
              });
        },
        delErr(){
          this.showNoData = true
        },
        initDatas(res){
            const _this = this;
//            console.log("获取到最近浏览的数据:",res);
          if(res.status === 200 && res.data.data!==null){
            this.articles = res.data.data.detail;
            const len1 = this.articles.length;
            len1>0 ? this.showNoData = false:this.showNoData = true;
            _this.newsData = [];
            _this.tieZiData = [];
            for(let i=0;i<len1;i++) {
              //是帖子
              if (parseInt(_this.articles[i].article_type) === 1 && parseInt(_this.articles[i].status) === 1) {
                _this.tieZiData.push(_this.articles[i]);
              } else if (parseInt(_this.articles[i].article_type) === 0 && parseInt(_this.articles[i].status) === 1) {
                _this.newsData.push(_this.articles[i]);
              }
            }
          }else{
            this.showNoData = true;
          }
        },
      }
  }
</script>
<style scoped>
  @import "../../../assets/css/personalviews.css";
  @import "../../../assets/css/index.css";
</style>



